export default {
	data() {
		return {
			cartList: [],
			address: '',
			allChecked: true,
			num: 0
		}
	},
	onShow() {
			this.cartList = uni.getStorageSync("carts")
			this.address = uni.getStorageSync('address')
			let arr = this.cartList.every(v => v.checked)
			this.allChecked = arr === true ? true : false
	},
	methods: {
		bindChange(value, index) {
			if (value === "0") {
				uni.showModal({
					title: '确定要删除吗',
					success: res => {
						if (res.confirm) {
							this.cartList.splice(index, 1)
						} else {
							this.cartList[index].num = 1
							return
						}
						uni.setStorageSync("carts", this.cartList)
					},
				})
			}

			this.cartList[index].num = Number(value)
			uni.setStorageSync("carts", this.cartList)


		},
		async getAddress() {
			try {
				const res = await this.getSetting()
				let auth = res.authSetting["scope.address"]
				if (auth === false) {
					// 打开授权页面
					await this.openSetting()
				}
				const res2 = await this.getChooseAddress()
				const {
					provinceName,
					cityName,
					countyName,
					detailInfo
				} = res2
				res2.city = provinceName + cityName + countyName + detailInfo
				this.address = res2
				uni.setStorageSync('address', this.address)
			} catch (err) {
				console.log(err)
			}

		},
		// 获取通讯地址
		getChooseAddress() {
			return new Promise((resolve, reject) => {
				uni.chooseAddress({
					success(res) {
						resolve(res)
					},
					fail(err) {
						reject(err)
					}
				})
			})
		},
		// 获取授权状态
		getSetting() {
			return new Promise((resolve, reject) => {
				uni.getSetting({
					success(res) {
						resolve(res)
					},
					fail(err) {
						reject(err)
					}
				})
			})
		},
		// 打开授权页面
		openSetting() {
			return new Promise((resolve, reject) => {
				uni.openSetting({
					success(res) {
						resolve(res)
					},
					fail(err) {
						reject(err)
					}
				})
			})
		},
		// 点击单选框是触发
		handleRadio(index) {
			this.cartList[index].checked = !this.cartList[index].checked
			let arr = this.cartList.every(v => v.checked)
			this.allChecked = arr === true ? true : false
			uni.setStorageSync("carts", this.cartList)
		},
		// 点击全选框触发
		handleAll() {
			this.allChecked = !this.allChecked
			this.cartList.map(v => {
				return v.checked = this.allChecked ? true : false
			})
			uni.setStorageSync("carts", this.cartList)
		},
		// 点击结算触发
		settlePrice() {
			if (this.address === "") {
				wx.showToast({
					title: '请选择收货地址',
					icon: 'none',
					mask: true,
				});
				return
			}
			if (this.num === 0) {
				wx.showToast({
					title: '请选择商品',
					icon: 'none',
					mask: true,
				});
				return
			}
			// console.log("跳转")
			uni.navigateTo({
				url:'../pay/pay'
			})
		}

	},
	computed: {
		// 计算总价
		allPrice() {
			let arr = []
			let PriceObj = {
				unitPrice: 0,
				total: 0
			}

			if (this.cartList.length === 0) {
				this.allChecked = false
				return PriceObj
			}
			this.cartList.forEach(v => {
				if (v.checked) {
					PriceObj.unitPrice += v.goods_price * v.num
					PriceObj.total += v.num
				}
			})
			this.num = PriceObj.total
			return PriceObj
		}
	}
}
